@import url("https://fonts.googleapis.com/css2?family=Prosto+One&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,700;0,900;1,300;1,400;1,500;1,700;1,900&display=swap");
* {
  font-family: "Roboto", sans-serif;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Prosto One", cursive;
}

img {
  max-width: 100%;
}

body.hidden {
  overflow: hidden;
}

header.home {
  background-color: transparent;
  height: 100px;
  display: flex;
  align-items: center;
  position: relative;
  z-index: 9;
}
header.home .container-desc {
  justify-content: space-between;
}
header.home .container-desc .main-menu {
  display: flex;
  align-items: center;
}
header.home .container-desc .main-menu ul {
  list-style-type: none;
  padding-left: 0;
  margin-bottom: 0;
}
header.home .container-desc .main-menu ul li {
  margin-right: 20px;
}
header.home .container-desc .main-menu ul li a {
  font-family: "Prosto One", cursive;
  font-size: 18px;
  line-height: 25px;
  color: #006FBF;
  text-decoration: none;
  transition: color 0.3s linear;
}
header.home .container-desc .main-menu ul li a:hover {
  color: #FE8900;
}
header.home .container-desc .main-menu ul li.current-menu-item:not(.feedback-menu) a {
  color: #FE8900;
}
header.home .container-desc .social-links a {
  margin-left: 20px;
}
header.home .container-desc .mobile-logo {
  max-height: 60px;
  display: none;
}
header.home .container-desc .nav.mobile-menu {
  display: none;
  position: absolute;
  right: 15px;
  top: 30px;
  cursor: pointer;
}
header.home .container-desc .nav.mobile-menu span {
  display: block;
  height: 3px;
  background-color: #FE8900;
  margin: 5px 0 0;
  transition: all 0.1s linear 0.3s;
  width: 25px;
  z-index: 9;
  position: relative;
}
header.home .container-desc .nav.mobile-menu span.first {
  margin-top: 0;
}
header.home .container-desc .nav.mobile-menu .opened span.first {
  -webkit-transform: rotate(45deg);
  margin-top: 10px;
  margin-right: 6px;
}
header.home .container-desc .nav.mobile-menu .opened span.second {
  opacity: 0;
}
header.home .container-desc .nav.mobile-menu .opened span.third {
  -webkit-transform: rotate(-45deg);
  margin-top: -10px;
  margin-right: 6px;
}
header.home .container-desc .header-mobile-menu {
  display: none;
  position: absolute;
  z-index: 8;
  transition: all 0.3s;
  margin-bottom: 0;
}

@media (max-width: 991px) {
  header.home {
    height: 80px;
    background-color: #fff;
    position: fixed;
    top: 0;
    width: 100%;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  }
  header.home .container-desc {
    height: 100%;
    align-items: center;
    padding-right: 75px;
    position: relative;
  }
  header.home .container-desc .nav.mobile-menu {
    display: flex;
  }
  header.home .container-desc .header {
    position: fixed;
    background: white;
  }
  header.home .container-desc .main-menu {
    display: none;
  }
  header.home .container-desc .mobile-logo {
    display: block;
  }
  header.home .container-desc .header-mobile-menu.opened {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: rgba(19, 108, 156, 0.9);
    overflow-y: scroll;
    display: block;
    padding: 50px;
    list-style-type: none;
  }
  header.home .container-desc .header-mobile-menu.opened li {
    margin-bottom: 20px;
  }
  header.home .container-desc .header-mobile-menu.opened li a {
    color: #FE8900;
    font-size: 20px;
    font-weight: 500;
    line-height: 30px;
  }
  header.home .container-desc .header-mobile-menu.opened li.current_page_item:not(.feedback-menu) a {
    color: #fff;
  }
}
